import React from 'react';

import Header from '../Public/Header';
import Footer from '../Public/Footer';

import Config from '../config';
import Utils from '../utils';
import './Show.css';

class Show extends React.Component {

    constructor(){
        super();
        Utils.setTitle('首页');
        this.state = {
            slide: [],
            slideNow: 0,
            notice: {},
        };
        // 获滚取动
        fetch(Config.requestUrl+"Common/GetSlide", {method:"GET", mode: "cors"}).then(res=>res.text()).then(data=>{
            data = JSON.parse(data);
            this.setState({
                slide: data
            });
            let _this = this;
            setInterval(function(){
                let id = _this.state.slideNow;
                id++;
                if(id>=_this.state.slide.length) id = 0;
                _this.setState({
                    slideNow: id
                });
            }, 5000);
        });
        // 获滚公告
        fetch(Config.requestUrl+"Common/GetNotice", {method:"GET", mode: "cors"}).then(res=>res.text()).then(data=>{
            data = data.replace(" ", "").replace("\n", "").replace("    ", "");
            data = JSON.parse(data);
            this.setState({
                notice: data[0]
            });
        });
    }

    // 滚动
    slide(id){
        this.setState({
            slideNow: id
        });
    }

    // 弹出公告
    alertNotice(){
        alert(this.state.notice.content);
    }

    render(){
        return (
            <div className="main">
                <div className="w1000">
                    <Header />
                    <div className="w1000">
                        <div className="banner">
                            <div id="ad-flash">
                                <div className="focusBox">
                                    <ul className="pic">
                                        {this.state.slide.map(function(s, k){
                                            return <li key={k} className={k==this.state.slideNow?'on':''}>
                                                        <a href={s.url} target="_blank">
                                                            <img src={require('../Asset/Img/'+s.img)} />
                                                        </a>
                                                    </li>
                                        }, this)}
                                    </ul>
                                    <ul className="hd">
                                        {this.state.slide.map(function(s, k){
                                            return <li key={k} onClick={this.slide.bind(this, k)} className={k==this.state.slideNow?'on':''}></li>
                                        }, this)}
                                    </ul>
                                </div>
                            </div>
                            <div className="newbox">
                                <div className="newin">
                                    <div className="lunleft">
                                        <marquee scrollamount="8" scrolldelay="150" direction="left" onClick={this.alertNotice.bind(this)}>
                                            {this.state.notice.title+" - "+this.state.notice.content}
                                        </marquee>
                                    </div>
                                </div>
                                <div className="fr pt3"><img src={require('../Asset/Img/domain2.png')} alt="" /></div>
                            </div>
                            <div className="clear"></div>
                        </div>
                        <div className="clear h10"></div>
                        <ul className="game">
                            <li><a href="/visual" className="">
                                <div className=""><img src={require('../Asset/Img/yx1.png')} alt="" /></div>
                                <p className="da">真人娱乐</p>
                                <p className="xiao">LIVE DEALER</p>
                            </a></li>
                            <li><a href="/mg/index" className="">
                                <div className=""><img src={require('../Asset/Img/yx2.png')} alt="" /></div>
                                <p className="da">电子游艺</p>
                                <p className="xiao">SLOT GAME</p>
                            </a></li>
                            <li><a href="/cn/sports" className="">
                                <div className=""><img src={require('../Asset/Img/yx3.png')} alt="" /></div>
                                <p className="da">体育赛事</p>
                                <p className="xiao">SPORT GAME</p>
                            </a></li>
                            <li><a href="/cn/caipiao" className="">
                                <div className=""><img src={require('../Asset/Img/yx4.png')} alt="" /></div>
                                <p className="da">彩票游戏</p>
                                <p className="xiao">LOTTERY</p>
                            </a></li>
                            <li><a href="/vg" className="">
                                <div className=""><img src={require('../Asset/Img/yx5.png')} alt="" /></div>
                                <p className="da">棋牌游戏</p>
                                <p className="xiao">Board games</p>
                            </a></li>
                        </ul>
                    </div>
                    <div className="clear h15"></div>
                    <Footer />
                </div>
            </div>
        )
    }
}

export default Show;