import React from "react";

import Config from '../config';
import './Header.css';

class Header extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            licenseDisplay: {display: "none"},
            lotteryMenuDisplay: {display: "none"},
            lotteryList: [],
            date: this.getAmericanTime()
        };
        // 获取时间
        let _this =this;
        setInterval(function(){
            _this.setState({
                date: _this.getAmericanTime()
            });
        }, 1000)
        // 获取彩种列表
        fetch(Config.requestUrl+"Lottery/GetTypeList", {method:"GET", mode: "cors"}).then(res=>res.text()).then(data=>{
            data = JSON.parse(data);
            this.setState({
                lotteryList: data
            });
        });
    }

    // 显示牌照
    showLicense(){
        this.setState({
            licenseDisplay: {display: "block"}
        });
    }
    hideLicense(){
        this.setState({
            licenseDisplay: {display: "none"}
        });
    }

    // 显示彩票列表
    showLotteryMenu(){
        this.setState({
            lotteryMenuDisplay: {display: "block"}
        });
    }
    hideLotteryMenu(){
        this.setState({
            lotteryMenuDisplay: {display: "none"}
        });
    }

    // 获取美国时间
    getAmericanTime() {
        let date = new Date(new Date().getTime() - 12 * 3600 * 1000);
        let year = date.getFullYear();
        let month = date.getMonth()+1;
        let day = date.getDate();
        let hour = date.getHours();
        let minute = date.getMinutes();
        let second = date.getSeconds();
        if(month<10) month = "0"+month;
        if(day<10) day = "0"+day;
        if(hour<10) hour = "0"+hour;
        if(minute<10) minute = "0"+minute;
        if(second<10) second = "0"+second;
        return year+"-"+month+"-"+day+" - "+hour+":"+minute+":"+second;
    }

    render(){
        return (
            <div className="header">
                <div className="top">
                    <div className="fr">
                        <a href="https://ub.xf0371.com/UB/UB-Launcher.exe" target="_blank" rel="noopener noreferrer">寰宇浏览器</a>&nbsp;&nbsp;|&nbsp;&nbsp;
                        <a href="http://88850.cc" target="_blank" rel="noopener noreferrer">线路检测</a>&nbsp;&nbsp;|&nbsp;&nbsp;
                        <a href="javascript:;">防劫持教程</a>
                    </div>
                    <div className="clear"></div>
                </div>
                <div className="middle">
                    <div className="logobx">
                        <div className="logo">
                            <a href="/">
                                <img src={require('../Asset/Img/logo.png')} width="165" height="145" border="0" />
                            </a>
                        </div>
                    </div>
                    <div className="w1000">
                        <div className="h12"></div>
                        <div className="fl">
                            <div className="fl"><img src={require('../Asset/Img/domain.png')} alt="" /></div>
                            <div className="time" id="hktime">美东时间：{this.getAmericanTime()}</div>
                            <div className="clear h5"></div>
                            <div className="pai">
                                <a href="javascript:;" className="pz" onMouseOver={this.showLicense.bind(this)} onMouseOut={this.hideLicense.bind(this)}>官方牌照展示
                                    <img src={require('../Asset/Img/paizhao.png')} style={this.state.licenseDisplay} />
                                </a> |
                                <a href="http://agent.88850ww.com" target="_blank" className="">代理登入&nbsp;</a> |
                                <a href="/agent" className="">代理注册&nbsp;</a> |
                                <a href="/vg" className="textGlittera">开元棋牌 </a> |
                                <a href="javascript:;" className="lang"><img src={require('../Asset/Img/lang1.png')} alt="" /></a>
                                <a href="javascript:;" className="lang"><img src={require('../Asset/Img/lang2.png')} alt="" /></a>
                                <a href="javascript:;" className="lang"><img src={require('../Asset/Img/lang3.png')} alt="" /></a>
                            </div>
                        </div>
                        <div className="login fr">
                            <form name="LoginForm" id="myForm" action="/cn" method="POST" autoComplete="off">
                                <div className="loq">
                                    <div className="fl">
                                        <div className="shu fl">
                                            <input name="username" tabIndex="1" type="text" className="key1"
                                                   id="username" maxLength="20" placeholder="账号" />
                                        </div>
                                        <a href="javascript:;" className="fl">
                                            <img src={require('../Asset/Img/enter.png')} alt="" />
                                        </a>
                                        <div className="cl"></div>
                                        <div className="shu fl">
                                            <input name="password" tabIndex="2" type="password" className="key2"
                                                   id="password" maxLength="20" placeholder="······" />
                                        </div>
                                        <div className="fl playfor">
                                            <a href="/visual/ag-test" target="_blank">免费试玩</a>&nbsp;|&nbsp;
                                            <a href="/cn/forget">忘记密码？</a>
                                        </div>
                                    </div>
                                    <a href="/member/register" className="fr regbtn">免费开户</a>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
                <div className="nav">
                    <div className="w1000">
                        <div className="fl">
                            <ul>
                                <li className="fuli"><a href="/" className="fua">首页 </a></li>
                                <li className="fuli">
                                    <a href="/visual" className="fua">真人娱乐</a>
                                </li>
                                <li className="fuli">
                                    <a href="/mg/index" className="fua">电子游艺</a>
                                </li>
                                <li className="fuli">
                                    <a href="/cn/sports" className="fua">体育赛事</a>
                                </li>
                                <div className="clear"></div>
                            </ul>
                        </div>
                        <div className="fr">
                            <ul>
                                <li className="fuli" onMouseOver={this.showLotteryMenu.bind(this)} onMouseOut={this.hideLotteryMenu.bind(this)}>
                                    <a href="/cn/caipiao" className="fua">彩票游戏 </a>
                                    <div className="menu" style={this.state.lotteryMenuDisplay}>
                                        <div className="mein">
                                            <ul>
                                                {this.state.lotteryList.map(function(lottery, key){
                                                    return <li key={key}><a href="/jl">{lottery.name}</a></li>
                                                })}
                                            </ul>
                                        </div>
                                    </div>
                                </li>
                                <li className="fuli">
                                    <a href="/cn/youhui" className="fua"><em className="red">优惠活动</em></a></li>
                                <li className="fuli"><a href="/cn/mobile" className="fua">APP下载 </a></li>
                                <li className="fuli"><a
                                    href="https://messenger.providesupport.net/messenger/179g46bbyy1ag0ztiqokb7451y"
                                    className="popupkefu fua" target="_blank">在线客服</a></li>
                            </ul>
                        </div>
                        <div className="clear"></div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Header;