import config from "./config";

let utils = {};

// 设置标题
utils.setTitle = function(title){
    fetch(config.requestUrl+"Common/GetSetting", {method:"GET", mode: "cors"}).then(res=>res.text()).then(data=>{
        data = JSON.parse(data);
        document.title = title+"-"+data.appName;
    });
}

export default utils;